.step-card {
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.10);
  padding: 36px 32px 28px 32px;
  max-width: 600px;
  margin: 0 auto 28px auto;
  display: flex;
  flex-direction: column;
  gap: 22px;
  min-height: 520px;
  justify-content: space-between;
}

.step-header {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 12px;
}

.step-title {
  font-size: 1.7rem;
  font-weight: bold;
  color: #111418;
  letter-spacing: -0.015em;
}

.step-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.step-label {
  display: flex;
  flex-direction: column;
  font-size: 1.08rem;
  color: #111418;
  font-weight: 500;
  gap: 8px;
}

.step-input {
  border: none;
  background: #f0f2f5;
  border-radius: 12px;
  padding: 14px 16px;
  font-size: 1.08rem;
  color: #111418;
  outline: none;
  resize: none;
}

.step-textarea {
  min-height: 90px;
}

.step-submit {
  background: #0c7ff2;
  color: #fff;
  border: none;
  border-radius: 12px;
  padding: 16px 0;
  font-size: 1.15rem;
  font-weight: bold;
  cursor: pointer;
  margin-top: 10px;
  transition: background 0.2s;
}
.step-submit:hover {
  background: #0052cc;
}

.step-section {
  margin-bottom: 14px;
}

.step-section-title {
  font-size: 1.22rem;
  font-weight: bold;
  color: #111418;
  margin-bottom: 6px;
}

.step-section-desc {
  color: #60758a;
  font-size: 1.08rem;
  margin-bottom: 8px;
}

.step-details-grid {
  display: grid;
  grid-template-columns: 40% 1fr;
  gap: 0 18px;
}
.step-details-row {
  display: contents;
}
.step-details-label {
  color: #60758a;
  font-size: 1.05rem;
  font-weight: 500;
  padding: 10px 0;
}
.step-details-value {
  color: #111418;
  font-size: 1.05rem;
  padding: 10px 0;
}

.step-approval-row {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  color: #111418;
  font-size: 1.08rem;
}

.step-approval-reason {
  margin-top: 10px;
  background: #f9fafb;
  border-radius: 10px;
  padding: 12px 14px;
}
.step-approval-reason-title {
  font-weight: 600;
  color: #111418;
  margin-right: 10px;
}
.step-approval-reason-desc {
  color: #60758a;
  font-size: 1.05rem;
}

.step-project-overview {
  display: flex;
  gap: 20px;
  align-items: stretch;
}
.step-project-info {
  display: flex;
  flex-direction: column;
  gap: 6px;
  flex: 2 2 0px;
}
.step-project-id {
  color: #60758a;
  font-size: 1.05rem;
}
.step-project-name {
  color: #111418;
  font-size: 1.18rem;
  font-weight: bold;
}
.step-project-desc {
  color: #60758a;
  font-size: 1.05rem;
}
.step-project-img {
  flex: 1;
  min-width: 140px;
  aspect-ratio: 16/9;
  background-size: cover;
  background-position: center;
  border-radius: 12px;
}

.step-progress-bar-bg {
  background: #dbe0e6;
  border-radius: 10px;
  height: 12px;
  width: 100%;
  margin-bottom: 6px;
}
.step-progress-bar {
  background: #111418;
  border-radius: 10px;
  height: 12px;
}
.step-progress-label {
  color: #60758a;
  font-size: 1.05rem;
}

.step-metrics {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}
.step-metric {
  flex: 1 1 140px;
  background: #f9fafb;
  border: 1px solid #dbe0e6;
  border-radius: 12px;
  padding: 16px 12px;
  display: flex;
  flex-direction: column;
  gap: 6px;
  align-items: flex-start;
}
.step-metric span:last-child {
  color: #111418;
  font-size: 1.25rem;
  font-weight: bold;
}

.step-update {
  background: #f9fafb;
  border-radius: 10px;
  padding: 12px 14px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.step-update-title {
  font-weight: 600;
  color: #111418;
}
.step-update-date {
  color: #60758a;
  font-size: 1.05rem;
}
.step-update-desc {
  color: #60758a;
  font-size: 1.05rem;
}

/* Step导航按钮 */
.process-step-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 22px;
  margin: 0 auto 28px auto;
  max-width: 600px;
}
.process-step-btn {
  background: #0c7ff2;
  color: #fff;
  border: none;
  border-radius: 12px;
  padding: 12px 32px;
  font-size: 1.08rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}
.process-step-btn:disabled {
  background: #b6cfff;
  color: #fff;
  cursor: not-allowed;
}
.process-step-btn:hover:not(:disabled) {
  background: #0052cc;
}
.process-step-label {
  color: #60758a;
  font-size: 1.12rem;
  font-weight: 500;
} 

@media (max-width: 700px) {
  .step-card {
    max-width: 98vw;
    padding: 18px 6vw 16px 6vw;
  }
  .step-header {
    flex-direction: column;
    gap: 12px;
  }
  .step-title {
    font-size: 1.1rem;
  }
  .process-step-nav {
    max-width: 98vw;
    gap: 10px;
  }
  .process-step-btn {
    padding: 10px 10px;
    font-size: 0.98rem;
  }
} 