.process-container {
  background: linear-gradient(135deg, #0a1446 0%, #1a237e 60%, #3f51b5 100%);
  font-family: 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Arial', sans-serif;
  padding: 24px 0 40px 0;
  min-height: 100vh;
}

.process-section {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(10,20,70,0.18);
  padding: 28px 22px 22px 22px;
  max-width: 420px;
  margin: 0 auto 24px auto;
  color: #fff;
}

.process-title {
  font-size: 1.25rem;
  font-weight: bold;
  color: #42a5f5;
  margin-bottom: 18px;
}

.process-steps {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0 0 18px 0;
}

.process-step {
  flex: 1;
  text-align: center;
  position: relative;
  font-size: 1.05rem;
  color: #b6cfff;
  font-weight: 500;
  padding-bottom: 8px;
}
.process-step.active {
  color: #42a5f5;
  font-weight: bold;
}
.process-step:not(:last-child)::after {
  content: '';
  position: absolute;
  right: -50%;
  top: 50%;
  width: 100%;
  height: 3px;
  background: #22306b;
  z-index: 0;
}
.process-step.active:not(:last-child)::after {
  background: #42a5f5;
}

.process-desc {
  color: #e3eafc;
  font-size: 1.08rem;
  text-align: left;
  margin-bottom: 8px;
}

.process-contacts {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.process-contact-card {
  display: flex;
  align-items: center;
  background: rgba(66,165,245,0.10);
  border-radius: 12px;
  padding: 14px 12px;
  box-shadow: 0 2px 8px rgba(33,150,243,0.10);
}

.process-contact-avatar {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 16px;
  border: 2px solid #42a5f5;
}

.process-contact-name {
  font-weight: 600;
  color: #fff;
}

.process-contact-role {
  font-size: 0.98rem;
  color: #b6cfff;
}

.process-contact-email {
  font-size: 0.97rem;
  color: #42a5f5;
} 