.profile-container {
  background: linear-gradient(135deg, #0a1446 0%, #1a237e 60%, #3f51b5 100%);
  font-family: 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Arial', sans-serif;
  padding: 24px 0 40px 0;
  min-height: 100vh;
}

.profile-card, .profile-section {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(10,20,70,0.18);
  padding: 36px 32px 28px 32px;
  max-width: 600px;
  margin: 0 auto 28px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
}

.profile-header {
  display: flex;
  align-items: center;
}

.profile-avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #42a5f5;
  box-shadow: 0 2px 8px rgba(66,165,245,0.18);
}

.profile-info {
  margin-left: 32px;
}

.profile-title {
  font-size: 2rem;
  font-weight: bold;
  color: #42a5f5;
  margin: 0 0 10px 0;
}

.profile-role {
  color: #b6cfff;
  font-size: 1.2rem;
}

.profile-desc {
  margin-top: 32px;
  color: #e3eafc;
  font-size: 1.15rem;
  text-align: left;
}

.profile-stats {
  margin-top: 32px;
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.profile-stat {
  text-align: center;
}

.profile-stat-num {
  font-size: 1.4rem;
  font-weight: bold;
  color: #42a5f5;
}

.profile-stat-label {
  font-size: 1.05rem;
  color: #b6cfff;
}

.profile-section-title {
  font-size: 1.35rem;
  font-weight: bold;
  color: #42a5f5;
  margin-bottom: 22px;
}

.profile-jobs {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.profile-job {
  display: flex;
  align-items: flex-start;
}

.profile-job-icon {
  font-size: 2.2rem;
  margin-top: 2px;
}
.profile-job-icon-green {
  color: #22c55e;
}

.profile-job-info {
  margin-left: 20px;
}

.profile-job-title {
  font-weight: 600;
  color: #fff;
  margin: 0;
}

.profile-job-company {
  font-size: 1.05rem;
  color: #b6cfff;
}

.profile-job-desc {
  color: #e3eafc;
  margin-top: 8px;
  font-size: 1.08rem;
}

.profile-job-divider {
  border-top: 1px solid #22306b;
  margin: 20px 0;
}

.profile-courses {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.profile-course {
  display: flex;
  align-items: center;
  background: rgba(66,165,245,0.10);
  padding: 18px 16px;
  border-radius: 14px;
}

.profile-course-icon {
  font-size: 2.2rem;
}
.profile-course-icon-blue {
  color: #42a5f5;
}
.profile-course-icon-purple {
  color: #8b5cf6;
}
.profile-course-icon-red {
  color: #ef4444;
}

.profile-course-info {
  margin-left: 20px;
  flex-grow: 1;
}

.profile-course-title {
  font-weight: 600;
  color: #fff;
  margin: 0;
}

.profile-course-org {
  font-size: 1.05rem;
  color: #b6cfff;
}

.profile-course-arrow {
  font-size: 1.6rem;
  color: #9ca3af;
  margin-left: 10px;
}

@media (max-width: 700px) {
  .profile-card, .profile-section {
    max-width: 98vw;
    padding: 18px 6vw 16px 6vw;
  }
  .profile-header {
    flex-direction: column;
    gap: 12px;
  }
  .profile-info {
    margin-left: 0;
  }
  .profile-avatar {
    width: 80px;
    height: 80px;
  }
  .profile-title {
    font-size: 1.3rem;
  }
} 