.performance-container {
  background: linear-gradient(135deg, #0a1446 0%, #1a237e 60%, #3f51b5 100%);
  font-family: 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Arial', sans-serif;
  padding: 24px 0 40px 0;
  min-height: 100vh;
}

.performance-period-bar {
  display: flex;
  justify-content: center;
  gap: 18px;
  margin-bottom: 24px;
}

.performance-period-btn {
  background: linear-gradient(90deg, #2196f3 0%, #1976d2 100%);
  color: #fff;
  border: none;
  border-radius: 18px;
  padding: 8px 28px;
  font-size: 1.08rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(33,150,243,0.15);
}
.performance-period-btn.active {
  background: linear-gradient(90deg, #42a5f5 0%, #1565c0 100%);
  color: #fff;
  box-shadow: 0 4px 16px rgba(66,165,245,0.18);
}
.performance-period-btn:hover:not(.active) {
  background: linear-gradient(90deg, #42a5f5 0%, #1565c0 100%);
  color: #fff;
}

.performance-section {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(10,20,70,0.18);
  padding: 36px 32px 28px 32px;
  max-width: 600px;
  margin: 0 auto 28px auto;
  display: flex;
  flex-direction: column;
  gap: 18px;
  color: #fff;
}

.performance-metrics {
  display: flex;
  gap: 24px;
  justify-content: space-between;
}

.performance-metric {
  background: rgba(66,165,245,0.10);
  border-radius: 14px;
  padding: 18px 16px;
  flex: 1 1 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 2px 8px rgba(33,150,243,0.10);
}

.metric-label {
  color: #b6cfff;
  font-size: 1.05rem;
  margin-bottom: 8px;
}

.metric-value {
  display: flex;
  align-items: baseline;
  justify-content: center;
  gap: 4px;
  white-space: nowrap;
  font-size: 1.5rem;
  font-weight: bold;
}

.metric-currency {
  font-size: 1.1rem;
  margin-right: 2px;
}

.metric-number {
  font-size: 1.5rem;
  font-weight: bold;
}

.performance-chart-label {
  color: #42a5f5;
  font-size: 1.08rem;
  font-weight: 600;
  margin-bottom: 10px;
}

.performance-chart-placeholder {
  background: rgba(66,165,245,0.10);
  border-radius: 14px;
  height: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #b6cfff;
  font-size: 1.1rem;
}

.performance-title {
  font-size: 1.25rem;
  font-weight: bold;
  color: #42a5f5;
  margin-bottom: 10px;
}

.performance-ranking {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.performance-rank-item {
  display: flex;
  align-items: center;
  background: rgba(66,165,245,0.10);
  border-radius: 12px;
  padding: 14px 16px;
  box-shadow: 0 2px 8px rgba(33,150,243,0.10);
  gap: 18px;
}

.rank-index {
  font-size: 1.2rem;
  font-weight: bold;
  color: #42a5f5;
  width: 32px;
  text-align: center;
}

.rank-avatar {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 8px;
  border: 2px solid #42a5f5;
}

.rank-name {
  flex: 1;
  color: #fff;
  font-size: 1.08rem;
}

.rank-progress-bar-bg {
  flex: 1.5;
  background: #22306b;
  border-radius: 8px;
  height: 10px;
  margin: 0 12px;
  min-width: 80px;
  max-width: 120px;
  position: relative;
}
.rank-progress-bar {
  background: #42a5f5;
  border-radius: 8px;
  height: 10px;
  position: absolute;
  left: 0;
  top: 0;
}

.rank-value {
  color: #42a5f5;
  font-size: 1.15rem;
  font-weight: bold;
  min-width: 80px;
  text-align: right;
}

.performance-metric .metric-value {
  font-size: 1.5rem !important;
  font-weight: bold !important;
}

.personal-progress-card {
  background: rgba(66,165,245,0.10);
  border-radius: 14px;
  padding: 18px 16px;
  margin-bottom: 18px;
  box-shadow: 0 2px 8px rgba(33,150,243,0.10);
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.personal-progress-info {
  display: flex;
  gap: 24px;
  font-size: 1.08rem;
  color: #fff;
  justify-content: space-between;
  flex-wrap: wrap;
}

.personal-progress-target,
.personal-progress-actual,
.personal-progress-rate {
  font-weight: bold;
  color: #42a5f5;
}

.personal-progress-bar-bg {
  background: #e3eafc;
  border-radius: 8px;
  height: 12px;
  width: 100%;
  margin-top: 8px;
  position: relative;
}

.personal-progress-bar {
  background: #0c7ff2;
  border-radius: 8px;
  height: 12px;
  position: absolute;
  left: 0;
  top: 0;
}

@media (max-width: 700px) {
  .performance-section {
    max-width: 98vw;
    padding: 18px 6vw 16px 6vw;
  }
  .performance-metrics {
    flex-direction: column;
    gap: 12px;
  }
  .performance-period-bar {
    gap: 8px;
  }
  .performance-rank-item {
    flex-wrap: wrap;
    gap: 8px;
  }
  .rank-progress-bar-bg {
    min-width: 40px;
    max-width: 80px;
    margin: 0 4px;
  }
  .rank-value {
    min-width: 40px;
    font-size: 1rem;
  }
} 