body, .app-main {
  min-height: 100vh;
  margin: 0;
  font-family: 'Segoe UI', 'PingFang SC', Arial, sans-serif;
  background: linear-gradient(135deg, #0a1446 0%, #1a237e 60%, #3f51b5 100%);
  color: #fff;
}

.app-header {
  background: rgba(10, 20, 70, 0.95);
  box-shadow: 0 2px 16px rgba(33,150,243,0.10);
  padding: 32px 0 18px 0;
  border-radius: 0 0 32px 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-logo {
  height: 64px;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(33,150,243,0.12);
}

.main-visual-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 40px 0 100px 0;
  min-height: 400px;
}

.main-visual-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-visual {
  max-width: 90vw;
  max-height: 340px;
  border-radius: 24px;
  box-shadow: 0 8px 32px rgba(33,150,243,0.18);
  object-fit: cover;
  border: 2px solid rgba(66,165,245,0.18);
  position: relative;
  z-index: 2;
  animation: float 6s ease-in-out infinite;
  transition: all 0.3s ease;
}

.main-visual:hover {
  transform: scale(1.02);
  box-shadow: 0 12px 40px rgba(33,150,243,0.25);
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

.main-visual-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(66,165,245,0.15) 0%, transparent 70%);
  border-radius: 24px;
  z-index: 1;
  animation: glow 4s ease-in-out infinite alternate;
}

@keyframes glow {
  0% { opacity: 0.3; transform: translate(-50%, -50%) scale(1); }
  100% { opacity: 0.6; transform: translate(-50%, -50%) scale(1.1); }
}

.main-visual-pulse {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120%;
  height: 120%;
  border: 2px solid rgba(66,165,245,0.3);
  border-radius: 24px;
  z-index: 0;
  animation: pulse 3s ease-in-out infinite;
}

@keyframes pulse {
  0% { 
    opacity: 0.8; 
    transform: translate(-50%, -50%) scale(1);
    border-color: rgba(66,165,245,0.3);
  }
  50% { 
    opacity: 0.4; 
    transform: translate(-50%, -50%) scale(1.05);
    border-color: rgba(66,165,245,0.6);
  }
  100% { 
    opacity: 0.8; 
    transform: translate(-50%, -50%) scale(1);
    border-color: rgba(66,165,245,0.3);
  }
}

/* 添加文字动画效果 - 只在首页显示 */
.main-visual-container:has(.main-visual-wrapper)::before {
  content: "Rexel Innovation Platform";
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  color: #42a5f5;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  z-index: 3;
  opacity: 0;
  animation: fadeInUp 2s ease-out 0.5s forwards;
  text-shadow: 0 0 20px rgba(66,165,245,0.5);
}

@keyframes fadeInUp {
  0% { 
    opacity: 0; 
    transform: translateX(-50%) translateY(30px);
  }
  100% { 
    opacity: 1; 
    transform: translateX(-50%) translateY(0);
  }
}

/* 添加副标题动画 - 只在首页显示 */
.main-visual-container:has(.main-visual-wrapper)::after {
  content: "Empowering Digital Transformation";
  position: absolute;
  top: calc(20% + 60px);
  left: 50%;
  transform: translateX(-50%);
  color: #b6cfff;
  font-size: 1.2rem;
  text-align: center;
  z-index: 3;
  opacity: 0;
  animation: fadeInUp 2s ease-out 1s forwards;
}

.app-nav {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100vw;
  background: rgba(10, 20, 70, 0.95);
  box-shadow: 0 -2px 16px rgba(33,150,243,0.10);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 14px 0;
  border-radius: 32px 32px 0 0;
  z-index: 100;
}

.app-nav-btn, .custom-btn {
  background: linear-gradient(90deg, #2196f3 0%, #1976d2 100%);
  color: #fff;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 600;
  padding: 12px 32px;
  box-shadow: 0 4px 16px rgba(33,150,243,0.15), 0 1.5px 0 #fff2 inset;
  transition: background 0.2s, box-shadow 0.2s, transform 0.1s;
  letter-spacing: 1px;
  margin: 0 8px;
}

.app-nav-btn:hover, .custom-btn:hover {
  background: linear-gradient(90deg, #42a5f5 0%, #1565c0 100%);
  box-shadow: 0 6px 24px rgba(33,150,243,0.25), 0 2px 0 #fff3 inset;
  transform: translateY(-2px) scale(1.03);
}

.app-nav-btn.active {
  border-bottom: 3px solid #42a5f5;
  color: #42a5f5;
  background: linear-gradient(90deg, #42a5f5 0%, #1565c0 100%);
  box-shadow: 0 8px 32px rgba(66,165,245,0.18);
}

/* 卡片/内容区玻璃拟态 */
.card, .me-card, .profile-card, .supply-chain-card {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 18px;
  box-shadow: 0 8px 32px rgba(10,20,70,0.18);
  backdrop-filter: blur(8px);
  border: 1.5px solid rgba(255,255,255,0.12);
  color: #fff;
}

/* 适配原有卡片样式 */
.me-card, .profile-card, .supply-chain-card {
  padding: 32px 40px;
  margin: 24px auto;
  width: 340px;
  text-align: center;
}

/* 响应式适配 */
@media (max-width: 600px) {
  .main-visual-container {
    padding: 16px 0 80px 0;
  }
  .me-card, .profile-card, .supply-chain-card {
    width: 96vw;
    padding: 18px 6vw;
  }
  .app-header {
    padding: 18px 0 10px 0;
  }
} 

.dynamic-bg {
  position: fixed;
  z-index: 0;
  left: 0; top: 0; width: 100vw; height: 100vh;
  pointer-events: none;
  overflow: hidden;
}

.dynamic-lines {
  width: 100vw;
  height: 100vh;
  position: absolute;
  left: 0; top: 0;
}

.line1, .line2, .line3 {
  filter: blur(1.5px);
  opacity: 0.7;
  stroke-linecap: round;
  stroke-linejoin: round;
  animation: moveLine 8s linear infinite alternate;
}

.line2 { animation-delay: 2s; opacity: 0.5; }
.line3 { animation-delay: 4s; opacity: 0.3; }

@keyframes moveLine {
  0%   { transform: translateY(0px) scaleX(1); }
  100% { transform: translateY(-30px) scaleX(1.03); }
}

/* 让主内容在背景之上 */
.app-main, .app-header, .main-visual-container, .app-nav {
  position: relative;
  z-index: 1;
} 