.me-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  background: #f5f7fa;
}

.me-card {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
  padding: 32px 40px;
  text-align: center;
  width: 340px;
}

.me-avatar {
  width: 96px;
  height: 96px;
  border-radius: 50%;
  margin-bottom: 16px;
  object-fit: cover;
  border: 3px solid #1976d2;
}

.me-position {
  color: #1976d2;
  margin-bottom: 16px;
}

.me-info {
  text-align: left;
  margin: 16px 0 24px 0;
  color: #444;
  font-size: 15px;
}

.me-actions {
  display: flex;
  justify-content: space-between;
}

.me-btn {
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 8px 20px;
  font-size: 15px;
  cursor: pointer;
  transition: background 0.2s;
}

.me-btn.logout {
  background: #e53935;
}

.me-btn:hover {
  opacity: 0.85;
} 